<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import data from "../../assets/data.json";
import data3 from "../../assets/data3.json";


export default {
  data() {
    return {
      lang: "zhCHS",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
        tab: 1,
      timer: null,
      timer2: null,
      TokenPrice: 0,
        list:[],
        isSpecial:false,
        isNew:true
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account",]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
     changeCion(type) {
      this.tab = type;
      if(type==2){
         this.getList();
      }

    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    getList() {
       if (this.hmdInfo == null) {
        return;
      }
     var _this = this;
			_this.list = [];
			var investsLength = _this.hmdInfo.info[13];
			var times = parseInt(investsLength) - 1;
            var account = _this.account;
      
      for (let i = times; i > -1; i--) {
        
      this.mxtContract.methods
        .getInvestInfo(
      account,
          i
        )
        .call()
        .then((res) => {
          	//prop tips
							var investAmount = res[1];	//投资的金额
							var limitAmount = res[2];	//预计收益
							var earnAmount = res[3];	//当前收益
							var time = res[4];	//投资时间
              if(i==0){
                earnAmount=Number(earnAmount)-Number(_this.hmdInfo.info[3])
              }
              console.log(Number(investAmount)*2>Number(earnAmount))
             if(investAmount>0 ){
              if(Number(investAmount)*2 != Number(earnAmount)){
                    _this.list.push({
                    times: i,
                    investAmount: investAmount,
                    earnAmount: earnAmount,
                    limitAmount: limitAmount,
                    time: time
                  });
              }
                
              }else{
                _this.list=[];
              }

                  
          	_this.list.sort(_this.sortNumber);
            
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

			
			}
			// _this.seedHistory = true;
			// _this.beginTimer()
      console.log(_this.list)
    },
     sortNumber (a, b) {
      var t1=a.time;
      var t2 = b.time;
      
      return t2 - t1
  },
    getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .onExtract()
        .send({ from: account})
        .then((res) => {
          console.log("onExtract", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

    finalDynIncome: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .finalDynIncome()
        .send({ from: account})
        .then((res) => {
          console.log("finalDynIncome", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }

    var date = new Date();

    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      // this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
            // var newM = _this.hmdInfo.info[42]/10**18;
            // if(Number(newM)==10000){
            //   _this.isNew=true;
            // }
            //   data.forEach(element => {
            //     if((_this.hmdInfo.inviteCode.toUpperCase()==element.address.toUpperCase())){
            //       _this.isNew=true;
            //     }
            //   });
             data3.forEach(element => {
                if((_this.hmdInfo.inviteCode.toUpperCase()==element.address.toUpperCase())){
                  _this.isSpecial = true;
                  // _this.stakeAmount2=element.amount;
                  // _this.stakeAmount=element.amount;
                  //   _this.isOpen=true;
                  //    _this.getNbn(_this.stakeAmount, 1);
                }
              });
              
              console.log('data===',date,_this.hmdInfo.info[22])


          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

// setTimeout(function(){
//        _this.getList();
//     },3000)
    

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 30000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

