        padding: 15px 10px;
        text-align: center;

        span {
          color: #fff;

          img {
            margin-right: 5px;
            vertical-align: middle;
          }
        }

        h3 {
          text-align: center;
          color: #c38dff;
          margin: 5px 0 0 0;
        }
      }
    }

    .ming_bix {
      background: url(../../assets/img/info/info.png) top center no-repeat;
      background-size: 100% 100%;
      width: 92%;
      margin: 0 auto;
      padding:20px 25px 30px;
      box-sizing: border-box;

      span {
        color: #fff;
        display: block;
        margin-top: 23px;
      }

      h3 {
        font-family: DINPro;
        font-weight: bold;
        color: #FFFFFF;
        font-size: 26px;
        line-height: 24px;
        margin-top: 10px;

        small {
          color: #B16BFF;
          font-size: 15px;
        }
      }

      .btn {
        width: 120px;
        background: url(../../assets/img/info/btn.png) top center no-repeat;
        background-size: 100% 100%;
        border-radius: 30px;
        margin: 15px auto 0;
        display: block;
        border: none;
        text-align: center;
        color: #fff;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        font-size: 16px;
        padding: 10px;
      }

      // .btn_no{
      //    background: linear-gradient(#c9d0d1, #79797a);
      //   box-shadow: 0 3px 6px #bec1c7;
      //   border-radius: 30px;
      //   margin: 5px auto 0;
      //   display: inline-block;
      //   border: none;
      //   text-align: center;
      //   color: #fff;
      //   font-family: PingFangSC-Medium, PingFang SC;
      //   font-weight: 500;
      //   font-size: 18px;
      //   padding: 6px 20px;
      // }
    }
  }
}
</style>



