<script type="es6">
import HeadTop from "../../components/Header";
import data from "../../assets/data.json";
import data2 from "../../assets/data2.json";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "zhCHS",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      isFiveApprove: false,
      stakeAmount: null,
