      timer2: null,
      show: true,
      isHome: true,
      nbn: 0,
      isApproveGrcc: false,
      cionType: 'five',
      isOpen: true,
      tab: 1,
       isFranslation: true,
       isnew:true
    };
  },
  computed: {
    ...mapState("main", [
      // "priceHMD",
      "oldInfo",
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      "hmdBalance",
      "FiveCoinContract",
      // "nftInfo",
      "FiveDead",
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
      // "add_nftContract",
      "add_oldInfo"
    ]),
    activeUserFun:function(){
      if (this.mxtContract == null && this.oldInfo ==null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
       var money = 0;
       if(this.tab==2){
        money = Number(this.stakeAmount)*0.7;
       }else{
        money = Number(this.stakeAmount2);
       }
      var _this = this;
      var account = _this.account;
      if (Number(money) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }


      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      

      this.$toast.loading({
        // message: this.$t("message.pack"),.toLowerCase()
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .activeUser()
        .send({
          from: account,
        })
        .then((res) => {
          console.log("activeUser", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.isFranslation=false;
          _this.stakeAmount='';
          _this.stakeAmount2=''; 
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changes: function (){
       if (this.mxtContract == null && this.oldInfo ==null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }

      var _this = this;
      var inviteCode = _this.oldInfo.inviteCode.toString();
      var referrer = _this.oldInfo.referrer.toString();
      var account = _this.account;
      // var account = "0xfCE3cCa52EAbaa1f4bbEEEFa9f71ee438DD543e3";
      

      this.$toast.loading({
        // message: this.$t("message.pack"),.toLowerCase()
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account.toLowerCase(), inviteCode.toLowerCase(),referrer.toLowerCase(),"0xd078fe5bd5f2886347a1128f6976e90568aca816")
      this.mxtContract.methods
        .excUserDataSyn()
        .send({
          from: account,
        })
        .then((res) => {
          console.log("excUserDataSyn", res);
          _this.$toast.clear();
