      //   _this.$toast(_this.$t("message.max"));
      //   return;
      // }

      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      console.log(inviteCode, referrer, web3.utils.toWei(_this.stakeAmount2.toString(), 'ether'));
