        .investIn(inviteCode, referrer, web3.utils.toWei(_this.stakeAmount2.toString(), 'ether'))
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount2 = '';
          _this.nbn = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },

    // Five授权
    FiveApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.FiveCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isFiveApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    FiveisApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.FiveCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isFiveApprove = true;
          }
        });
    },

    // USDT授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.hmdInfo) {
        if (this.hmdInfo.referrer) {
          var addr = this.hmdInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?invite=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun', res,account,address)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    get_param: function (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      // console.log(r)
      return null;
    },
  },
  created() {
    this.add_mxtCoinContract();
    this.getCoinBalance();
  },
  mounted() {
    //  var isNameList=false;
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }


    var getData = () => {
      _this.add_hmdInfo();
      _this.add_mxtCoinContract();
      _this.add_hmdCoinContract();
      // _this.add_nftContract();
      _this.add_oldInfo()
    };

    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        // || !_this.oldInfo
        if (!_this.hmdInfo || !_this.mxtCoinContract || !_this.hmdBalance  ) {
          getData();
        } else {

           if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }

          if(_this.hmdInfo.info[22]==0){
            _this.stakeAmount=web3.utils.fromWei(_this.hmdInfo.info[2], "ether")*0.2;
            _this.stakeAmount2=web3.utils.fromWei(_this.hmdInfo.info[2], "ether")*0.2;
          }

          
          if(_this.hmdInfo.inviteCode || _this.hmdInfo.referrer){
            _this.isFranslation=false;
            _this.isnew=false;
          }

          if(!_this.oldInfo.inviteCode||!_this.oldInfo.referrer||! _this.oldInfo.info[1]>0){
               _this.isFranslation=false;
              //  _this.isnew=false;
          }
          if(!_this.oldInfo.inviteCode||!_this.oldInfo.referrer){

          }
console.log(_this.isnew)
          
            //  data2.forEach(element => {
            //     if((_this.account.toLowerCase()==element.address.toLowerCase())){
            //       _this.stakeAmount2=element.amount;
            //       _this.stakeAmount=element.amount;
            //         _this.isOpen=true;
            //          _this.getNbn(_this.stakeAmount, 1);
            //     }
            //   });
              //  data.forEach(element => {
              //       if((_this.account.toLowerCase()==element.address.toLowerCase()) && (_this.hmdInfo.info[1] ==0)){
              //         _this.stakeAmount2=element.amount;
              //         _this.stakeAmount=element.amount;
              //          _this.isOpen=true;
              //             _this.getNbn(_this.stakeAmount, 1);
              //       }
              //     });

          _this.getCoinBalance();
          _this.isApproveFun();
          _this.FiveisApproveFun();
          clearInterval(_this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    _this.timer2 = setInterval(() => {
      if (_this.timer) {
        clearInterval(_this.timer);
        _this.timer = null;
      }
      getDataCalculate();
    }, 8000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

