// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueI18n from 'vue-i18n'
import * as filters from './filters/filters.js'


import LangEn from '../static/lang/en'
import LangZhCHS from '../static/lang/zhCHS'
import LangZhCHT from '../static/lang/zhCHT'


import store from "./store";

import Vant from 'vant';
import 'vant/lib/index.css';

  


import VueClipboard from 'vue-clipboard2';

Vue.use(VueClipboard)
Vue.use(Vant);
Vue.use(Vant);
Vue.prototype.ArrList={
    oldArr:"0xc801e0c7ab7effe5fcf95d9d363eb83e441242ef",
    dappArr:"0x09c1735151e90D8A8630F2d4aeC3c290c1637635",
    UsdtArr:"0x55d398326f99059fF775485246999027B3197955",
    FiveArr:"0xd47adda2a0491fda35892ddc0bc85bf01adec8d1",
    GtArr:"0x1FA2f1562e4e652F69087D546E04bBaC53d9856A",
    AesArr:"0x6485accb893eac4f4de15b6ea7c1192f96db88ca",
    XhArr:"0x000000000000000000000000000000000000dead",
    AesLpArr:"0xa158c04f7b68953e6b7b0eef9c795555d7939c88",//购买NFT
    NFTArr:"0x56c361232095D1223c0258fb213DEf1354aB3964",//NFT
}

Vue.config.productionTip = false;

Object.keys(filters).forEach(key=>{
    Vue.filter(key,filters[key])//插入过滤器名和对应方法
})

Vue.use(VueI18n);



const i18n = new VueI18n({
    locale: 'zhCHS', // 语言标识
    messages: {
        'en': LangEn,
        'zhCHS': LangZhCHS,
        'zhCHT': LangZhCHT
    }
});



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
});


