<template>
  <div class="info">
      <button @click="investInByVirtual()" class="btn">创建虚拟账号</button>
       <button @click="changeUserLevel()" class="btn">更改上级用户等级</button>
       <button @click="changeUserLevel2()" class="btn">更改当前用户等级</button>
        <button @click="registerUserInfo()" class="btn">更改当前用户邀请链接</button>
        <button @click="changeHappy()" class="btn">更改收益</button>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import data2 from "../../assets/data2.json";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      link: "--",
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
      //   link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account","oldInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract", "add_oldInfo"]),
        //领取分红奖励
    sFun: function (add) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
       var info = {};
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .getUserInfo(add,1).call()
        .then((res) => {
          info.info = res[0];
        info.inviteCode = res[1];
        info.referrer = res[2];
          console.log(JSON.stringify(info));
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeHappy: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .changeHappy("0x69A8618CFD0cd6957915E821E58c54eC053ca64A",1,'4000000000000000000')
        .send({ from: account })
        .then((res) => {
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
     registerUserInfo: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .registerUserInfo("0x47b482e8440984099af04d01a5960ffae14579ec","0x47b482e8440984099af04d01a5960ffae14579ec","0xb74ea84354161cb4e8147e69289a41ca452a8bfd")
        .send({ from: account })
        .then((res) => {
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investInByVirtual: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .investInByVirtual("0x694df7cA41e79825B494c78ff31BcD86359E59AD","0x694df7cA41e79825B494c78ff31BcD86359E59AD","0xa408db9fd6829a102808c581d61a9df2fc4010aa",'100000000000000000000')
        .send({ from: account })
        .then((res) => {
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeUserLevel: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .changeUserLevel("0x694df7cA41e79825B494c78ff31BcD86359E59AD",4,1)
        .send({ from: account })
        .then((res) => {
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
     changeUserLevel2: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .changeUserLevel("0xa408db9fd6829a102808c581d61a9df2fc4010aa",5,0)
        .send({ from: account })
        .then((res) => {
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    var getData = () => {
      this.add_hmdInfo();
      this.add_oldInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
            // data2.forEach(element => {
            //   _this.sFun(element.address);
            //       });
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    // this.timer2 = setInterval(() => {
    //   if (this.timer) {
    //     clearInterval(this.timer);
    //     this.timer = null;
    //   }
    //   getDataCalculate();
    // }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  .van-count-down {
    color: #75a9db;
  }
background: linear-gradient(104deg,#0f1d28,#03030D,#03030D);
padding-top:60px;

  .title {
    font-size: 26px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #4da9db;
    line-height: 39px;
    margin: 0 0 15px;

    span {
      color: #ffa0ae !important;
      font-size: 26px !important;
      font-weight: bold !important;
    }
  }
  .line_box {
    width:92%;
    margin: 30px auto 20px;
    background: url(../../assets/img/fan/min_bg.png) top center no-repeat;
    background-size: 100% 100%;
    position: relative;
    padding: 25px 20px;
    box-sizing: border-box;

    span {
      display: inline-block;
      color: #583baf;
     background: rgba(255,255,255, .5);
      position: absolute;
         right: 0;
    top: 10px;
    padding: 5px 12px;
    border-radius: 30px 0 0 30px;
    }

    .bt {
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #000;
      margin-bottom: 10px;
    }

    h2 {
      font-size: 30px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: bold;
      color: #fff;
    }

    h4 {
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #fff;
    }
  }

  .cent {
    width: 92%;
    margin: 30px auto;
    padding: 20px 0 50px;
    position: relative;
    background: #141634;
    border-radius: 15px;
  }

}
  .btn{
    width: 150px;
    margin: 0 auto;
    display: block;
      background: url(../../assets/img/fan/btn2.png) top center no-repeat;
    background-size: 100% ;
  border: none;
  text-align: center;
  color:#fff;
  font-family: PingFangSC-Medium, PingFang SC;
font-weight: 500;
font-size: 17px;
// border-radius: 10px;
padding:12px 22px;
  }
.my {
     background: url(../../assets/img/fan/min_bg2.png) top center no-repeat;
    background-size: 100% 100%;
  // padding: 30px 20px;
  box-sizing: border-box;
  width: 90%;
  margin: 0 auto 20px;
  position: relative;
  z-index: 2;
  padding: 10px 10px 15px;
span{color: #fff;margin:10px 0 5px;display: block;}
h3{
  font-size:18px;
font-family: DINPro;
font-weight: bold;
color: #02FEF5;
}
}

.list {
  // background: #fff;
  border-radius: 0 0 15px 15px;
  // padding: 20px;
    width: 90%;
  margin: 0 auto 20px;
  box-sizing: border-box;
  .line{
    background: #0C0D1F;
    border-radius: 10px;
    margin:10px 0;
    padding: 5px 10px;
  }
  span {
    display: inline-block;
    padding: 6px 0;
    color: #A6B2DC;
  }

  .textRight span {
    font-size: 15px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 600;
    color: #4da9db;
    overflow: hidden;
          display: block;
          width: 100%;
          text-align: right;
          text-overflow: ellipsis;
  }
}
</style>



